import React, { useState, useEffect } from 'react';
import { LineChart, Line, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer, Legend, Brush } from 'recharts';
import domtoimage from 'dom-to-image';
import './App.css';

const App = () => {
  const [data, setData] = useState([]);
  const [timeframe, setTimeframe] = useState('daily');

  useEffect(() => {
    fetch('/data.json')
      .then(response => response.json())
      .then(data => setData(data));
  }, []);

  const handleTimeframeChange = (newTimeframe) => {
    setTimeframe(newTimeframe);
  };

  const handleExport = () => {
    const chartContainer = document.getElementById('chart-container');

    domtoimage.toJpeg(chartContainer, { quality: 0.95 })
      .then((dataUrl) => {
        const link = document.createElement('a');
        link.download = 'chart.jpg';
        link.href = dataUrl;
        link.click();
      })
      .catch(error => console.error('Error exporting chart:', error));
  };

  const filterDataByTimeframe = (data, timeframe) => {
    const today = new Date();

    switch (timeframe) {
      case 'daily':
        return data;
      case 'weekly':
        const weekStart = new Date(today);
        weekStart.setDate(today.getDate() - 7);
        return data.filter(d => new Date(d.timestamp) >= weekStart);
      case 'monthly':
        const monthStart = new Date(today);
        monthStart.setMonth(today.getMonth() - 1);
        monthStart.setDate(1);
        return data.filter(d => new Date(d.timestamp) >= monthStart);
      default:
        return data;
    }
  };

  const filteredData = filterDataByTimeframe(data, timeframe);

  return (
    <div className="App">
      <h1>Chart Example</h1>
      <div className="button-group">
        <button onClick={() => handleTimeframeChange('daily')}>Daily</button>
        <button onClick={() => handleTimeframeChange('weekly')}>Weekly</button>
        <button onClick={() => handleTimeframeChange('monthly')}>Monthly</button>
      </div>
      <div className="chart-container" id="chart-container">
        <ResponsiveContainer width="100%" height={400}>
          <LineChart data={filteredData}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis dataKey="timestamp" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line type="monotone" dataKey="value" stroke="#8884d8" />
            <Brush dataKey="timestamp" height={30} stroke="#8884d8" />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <button className="export-button" onClick={handleExport}>Export as JPG</button>
    </div>
  );
};

export default App;
